<template>
  <div>
    <p>:</p>
    <p><input type="text" name="name" v-model="title" /></p>

    <p>:</p>
    <p><input name="species" type="text" v-model="subtitle" /></p>

    <p>:</p>
    <p><input name="species" type="text" v-model="cost" /></p>

    <p>:</p>
    <p>
      <textarea name="comments" rows="5" cols="100" v-model="remark"></textarea>
    </p>
    <p>:</p>
    <p>
      <label>1<input type="radio" v-model="prim" value="1"/></label>
      <label>2<input type="radio" v-model="prim" value="2"/></label>
      <label>3<input type="radio" v-model="prim" value="3"/></label>
      <label>4<input type="radio" v-model="prim" value="4"/></label>
      <label>5<input type="radio" v-model="prim" value="5"/></label>
    </p>
    <p>:</p>

    <template v-for="category in categoryList">
      <h4>{{ category.categoryTitle + ":" }}</h4>
      <template v-for="content in category.content">
        <label
          >{{ content
          }}<input
            :type="category.type == 0 ? 'radio' : 'checkbox'"
            v-model="categoryOpt"
            :value="content"/></label
        ><br />
      </template>
    </template>

    <div class="page-upload">
      <input type="file" @change="handleFileUpload" name="0" />
      <img ref="img0" class="page-imgStyle" />
      <a :href="imgs[0]">{{ imgs[0].length > 0 ? "" : "" }}</a>
    </div>
    <div class="page-upload">
      <input type="file" @change="handleFileUpload" name="1" />
      <img ref="img1" class="page-imgStyle" />
      <a :href="imgs[1]">{{ imgs[1].length > 0 ? "" : "" }}</a>
    </div>
    <div class="page-upload">
      <input type="file" @change="handleFileUpload" name="2" />
      <img ref="img2" class="page-imgStyle" />
      <a :href="imgs[2]">{{ imgs[2].length > 0 ? "" : "" }}</a>
    </div>
    <div class="page-upload">
      <input type="file" @change="handleFileUpload" name="3" />
      <img ref="img3" class="page-imgStyle" />
      <a :href="imgs[3]">{{ imgs[3].length > 0 ? "" : "" }}</a>
    </div>

    <p><button v-on:click="sendInfo()"></button></p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      id: -1,
      title: "",
      subtitle: "",
      cost: 0,
      remark: "",
      categoryList: [
        {
          sort: 0,
          categoryID: 0,
          categoryTitle: "",
          type: 1,
          content: ["", "", "", "", ""]
        },
        {
          sort: 1,
          categoryID: 1,
          categoryTitle: "",
          type: 1,
          content: ["", "", "", ""]
        }
      ],
      categoryOpt: [],
      imgs: ["", "dfgdfg", "dfgdfg", "dfgdfgdf"],
      prim: 1
    };
  },
  async created() {
    if (this.id < 0) {
      console.log("");
    } else {
      console.log("");
    }
  },
  methods: {
    handleFileUpload(event) {
      const file = event.target.files[0];
      const formData = new FormData();
      formData.append("file", file);
      // formData

      console.log(this.$refs["img" + event.target.name]);
      // console.log(event.target.name);
      // const myDiv = this.$refs["img" + event.target.value];
    },
    sendInfo() {
      console.log("ID", this.$route.params.id);
      console.log("", this.title);
      console.log("", this.subtitle);
      console.log("", this.cost);
      console.log("", this.remark);
      console.log("", parseInt(this.prim));
      console.log("", this.categoryOpt);
    }
  }
};
</script>
<style scoped>
.page-imgStyle {
  width: 148px;
  height: 121px;
}
</style>



