<template>
  <div>
    <div class="cms-item-menu">
      <div class="cms-item-menu-left">
        <button class="button" v-on:click="del()"></button>
      </div>
      <div class="cms-item-menu-fight">
        <button class="button" v-on:click="add($event)"></button>
      </div>
    </div>
    <div class="cms-item-wrap">
      <table class="cms-item-table">
        <colgroup>
          <col style="width: 3%;" />
          <col style="width: 10%;" />
          <col style="width: 10%;" />
          <col style="width: 3%;" />
          <col style="width: 5%;" />
          <col style="width: 30%;" />
          <col style="width: 20%;" />
        </colgroup>
        <tr>
          <th></th>
          <th></th>
          <th><br /></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(item, idx) in list">
          <td><input type="checkbox" :value="item.id" /></td>
          <td>
            <img class="imgStyle" :src="item.imgs[0]" />
            <img class="imgStyle" :src="item.imgs[1]" /><br />
            <img class="imgStyle" :src="item.imgs[2]" />
            <img class="imgStyle" :src="item.imgs[3]" />
          </td>
          <td>{{ item.title }}<br />{{ item.subTitle }}</td>
          <td>{{ item.cost }}</td>
          <td>{{ item.prim }}</td>
          <td>
            <template v-for="(catergory, idx) in item.catergory">
              {{ catergory.content }},
            </template>
          </td>
          <td>
            <button class="button" :value="item.id" v-on:click="edit($event)">
              
            </button>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
export default {
  data() {
    return {
      list: [
        {
          id: 1,
          title: "",
          subTitle: "wwww",
          catergory: [
            { catergoryID: 1, categoryTitle: "", content: "" }
          ],
          cost: 10,
          remark: "",
          size: "1X5",
          imgs: [
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg"
          ],
          prim: 4
        },
        {
          id: 2,
          title: "",
          subTitle: "wwww",
          catergory: [
            { catergoryID: 1, categoryTitle: "", content: "" },
            { catergoryID: 1, categoryTitle: "", content: "" }
          ],
          cost: 100,
          remark: "",
          size: "1X5",
          imgs: [
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg"
          ],
          prim: 4
        },
        {
          id: 3,
          title: "",
          subTitle: "wwww",
          catergory: [
            { catergoryID: 1, categoryTitle: "", content: "" }
          ],
          cost: 156,
          remark: "",
          size: "1X5",
          imgs: [
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg",
            "http://127.0.0.1:8080/static/img/logo.fd36ac9.svg"
          ],
          prim: 4
        }
      ]
    };
  },
  methods: {
    del() {
      let delArr = [];
      $("input[type=checkbox]:checked").each(function() {
        delArr.push(parseInt(this.value));
      });
      this.list = this.list.filter(e => {
        return delArr.indexOf(e.id) > -1;
      });

      console.log(this.list);

      //TODO:del API
    },
    edit(event) {
      // console.log(event.target.value);
      this.$router.push("/cms/page/" + event.target.value);
    },
    add(event) {
      console.log(event);
      this.$router.push("/cms/page/new");
    }
  }
};
</script>
<style scoped>
.cms-item-menu {
  display: flex;
  justify-content: space-between;
}

/*  */
.cms-item-wrap {
  margin-top: 5px;
  overflow: hidden;
  border-radius: 10px 10px 0px 0px;
}

.cms-item-table {
  font-family: "Oswald", sans-serif;
  border-collapse: separate;
  width: 100%;
  border-spacing: 0;
  border: 1px solid #6b675d;
}

.cms-item-table th {
  background-color: #3a70aa;
  color: #ffffff;
  width: 25vw;
  height: 50px;
}

.cms-item-table td {
  width: 1%;
  height: 30px;
  background-color: rgb(243, 243, 243);
  text-align: center;
}
.imgStyle {
  width: 50px;
  height: 50px;
}
.cms-item-ul li {
  display: list-item;
}
</style>



